export default {
  routerName: 'FactorForm',
  cols: [{
      th: "",
      td: "factor.date",
      type: "date",
      sortable: false,
      filters: [{
          label: "xxxx-xx-xx   ",
          model: "date__gte"
        },
        {
          label: "xxxx-xx-xx  ",
          model: "date__lte"
        }
      ]
    },
    {
      th: " ",
      td: "factor.type",
      type: "select",
      sortable: false,
      options: [{
        label: '',
        value: 'buy'
      }, {
        label: '  ',
        value: 'backFromBuy'
      }, {
        label: '',
        value: 'sale'
      }, {
        label: '  ',
        value: 'backFromSale'
      }, ],
      filters: ['factor.type']
    },
    {
      th: " ",
      td: "factor.code",
      type: "number",
      sortable: false,
      filters: ["factor.code"]
    },
    {
      th: " ",
      td: "factor.account.name",
      type: "text",
      sortable: false,
      sortable: false,
      filters: ["factor.account.name"]
    },
    {
      th: "",
      td: "input.count",
      type: "number",
      sortable: false,
      filters: []
    },
    {
      th: "",
      td: "input.fee",
      type: "money",
      sortable: false,
      filters: []
    },
    {
      th: "",
      td: "input.total",
      type: "money",
      sortable: false,
      filters: []
    },
    {
      th: "",
      td: "output.count",
      type: "number",
      sortable: false,
      filters: []
    },
    {
      th: "",
      td: "output.fee",
      type: "money",
      sortable: false,
      filters: []
    },
    {
      th: "",
      td: "output.total",
      type: "money",
      sortable: false,
      filters: []
    },
    {
      th: "",
      td: "remain.count",
      type: "number",
      sortable: false,
      filters: []
    },
    {
      th: "",
      td: "remain.fee",
      type: "money",
      sortable: false,
      filters: []
    },
    {
      th: "",
      td: "remain.total",
      type: "money",
      sortable: false,
      filters: []
    },
  ],
  colHeaders: [{
      colspan: '5',
      title: '',
    },
    {
      colspan: '3',
      title: '',
    },
    {
      colspan: '3',
      title: '',
    },
    {
      colspan: '3',
      title: '',
    },
  ]
}



